import PropTypes from 'prop-types'
import React from 'react'
import SlickSlider from 'react-slick'
import 'slick-carousel/slick/slick-theme.css'
import 'slick-carousel/slick/slick.css'
import './slider.scss'

const SliderDot = (page) => <button className="slider-dot">{page + 1}</button>

const defaultSettings = {
  dots: true,
  arrows: false,
  infinite: true,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  customPaging: SliderDot,
  responsive: [
    {
      breakpoint: 1280,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 1,
      },
    },
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1,
      },
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
}

const Slider = ({ children, settings }) => {
  const useSettings = { ...defaultSettings, ...settings }

  return (
    <section className="slider">
      <SlickSlider {...useSettings}>{children}</SlickSlider>
    </section>
  )
}

Slider.propTypes = {
  children: PropTypes.node,
  settings: PropTypes.object,
}

Slider.defaultProps = {
  settings: defaultSettings,
}

export default Slider
